import { h, Component } from 'preact';
import { observer } from 'mobx-preact';

import LineGraph from '../../components/lineGraph';
import wutil from '../../lib/wifet-util';


const wearData = wutil.makeRandomData({ cnt: 500, min: 0, max: 1000, onlyUp: true, inputData: [], diff: 2 });
const aiData = wutil.makeRandomData({ cnt: 500, min: 0, max: 1000, onlyUp: true, wearData, diff: 2 });

const initialState = {
	aiData,
	wearData,
};

@observer
class Wear extends Component {
    constructor(props) {
        super(props);
        this.state = {
            ...initialState,
        };
    }

	render() {
		const { wearData, aiData } = this.state;
		return (
			<div class='container-fluid mt-5 pt-2'>
				<div class='row'>
					<div class='col-12'>
						<h1>Wear</h1>
						<LineGraph
							stores={this.props.stores}
							data={wearData}
							dataB={aiData}
							legend='Wear'
							legendB='AI prediction'
							width={1200}
							height={500}
							yMax={1000}
							yMin={0}
							showYTicks={1}
							yTicks='["100%", "75%", "50%", "25%", "0%"]'
							unit='%'
							showXTicks={1}
							marker={400}
						/>
					</div>
				</div>
			</div>
		);
	}
}

export default Wear;
